<template>
	<view class="shopList_con">
		<view class="list">
			<view class="items flex" v-for="(item, index) in collectList" :key="index">
				<image :src="item.resUrl" mode="" class="shopImg"></image>
				<!-- objectType 0商街 1店铺 -->
				<view class="right" v-if="item.objectType == 1">
					<view class="tit">{{item.storeTitle}}</view>
					<!-- 店铺显示 -->
					<view class="area" style="margin-top: 23rpx;" >
						<text>{{item.streetTitle}}</text>
						<!-- <text class="xian"></text>
						<text>美好广场</text>
						<text>江汉区</text> -->
					</view>
					<view class="rent" style="margin-top: 23rpx;" >
						<text class="rental">{{item.rent}}</text>
						<text class="size">{{item.floorSpace}}㎡</text>
					</view>
					
					
				</view>
				<!-- 商街显示 -->
				<view class="right" v-else>
					<view class="tit">{{item.streetTitle}}</view>
					<view class="rent" style="margin-top: 21rpx;" >
						<text class="size">项目体量</text>
						<text class="rental">{{item.floorSpace}}㎡</text>
					</view>
					<view class="adrass">{{item.address}}</view>
				</view>
			</view>

		</view>
		<view class="shangla">{{loadMore}}</view>
	</view>
</template>

<script>
	export default {
		onLoad() {
			this.token = uni.getStorageSync('userInfo').token
			this.userId = uni.getStorageSync('userInfo').userId
			this.getList()
		},
		data() {
			return {
				collectList:[],
				beg: false, //控制发请求
				isloadMore: false,
				start: 0,
				limit: 10,
				loadMore:'-上拉加载更多-',
				userId: '',
				token: ''
			};
		},
		methods: {
			onReachBottom() {
				this.getList();
			},
			linkTo() {
				uni.navigateTo({
					url: '../businessShopDetils/businessShopDetils'
				})
			},
			// 商业项目列表
			getList() {
			
				if (this.beg || this.isloadMore) return
				let params = {
					userId: this.userId,
					start: this.start,
					limit: this.limit
				}
				uni.showLoading({
					title: '加载中'
				});
				this.$http.post(this.$api.collectList, {
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res => {
					if (res.data.code == 0) {
						this.collectList = [...this.collectList, ...res.data.data.list]
						this.start += 10
						let total = this.start;
						if (!res.data.data.list.length || total >= res.data.data.totalNum || res.data.data.totalNum <= this.limit) {
							this.isloadMore = true;
							this.loadMore = '-没有更多数据了-';
						}
						this.beg = false
						uni.hideLoading();
			
					}else{
						this.beg = false
					    this.toast(res.data.msg)
					}
				})
			},
		}
	}
</script>

<style lang="less" scoped>
	.shopList_con {
		width: 750rpx;

		.list {
			padding: 0 36rpx;
			box-sizing: border-box;

			.items {
				padding: 32rpx 0;
				box-sizing: border-box;
				border-bottom: 2rpx solid rgba(238, 238, 238, 1);

				.shopImg {
					width: 202rpx;
					height: 150rpx;
					border-radius: 10rpx;
				}

				.right {
					flex: 1;
					margin-left: 36rpx;

					.tit {
						font-size: 32rpx;
						font-family: PingFang SC;
						font-weight: 500;
						color: #343434;
					}

					.area {

						text {
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;

							&:nth-child(3) {
								margin-right: 11rpx;
							}
						}

						.xian {
							margin: 0 12rpx 0;
							width: 2rpx;
							height: 25rpx;
							background: #909090;
						}

					}

					.rent {

						.rental {
							font-size: 30rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #FF6634;
						}

						.size {
							font-size: 24rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;
						}
						text{
							&:nth-child(1){
								margin-right: 24rpx;
							}
						}
					}
					.adrass{
						margin-top: 28rpx;
						font-size: 24rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #909090;
						display: -webkit-box;
						-webkit-line-clamp: 1;
						overflow: hidden;
						text-overflow: ellipsis;
						-webkit-box-orient: vertical;
					}

				}
			}
		}
		.shangla{
			width: 100%;
			font-size: 24rpx;
			font-family: PingFang SC;
			font-weight: 400;
			color: #656565;
			margin-top: 46rpx;
			text-align: center;
		}
	}
</style>
